import { Box, Typography } from "@mui/material";
import { Carousel } from "primereact/carousel";
import { FC } from "react";
import bidenergy from "./../assets/clients/bidenergy.jpeg";
import bravoenergia from "./../assets/clients/bravoenergia.jpeg";
import climatempo from "./../assets/clients/climatempo.png";
import hospitalicarai from "./../assets/clients/hospitalicarai.png";
import oceaneering from "./../assets/clients/oceaneering.png";
import santamonica from "./../assets/clients/santamonica.jpeg";
import tenbrasil from "./../assets/clients/tenbrasil.png";
import styles from "./../styles/section.module.scss";
import { Section } from "./section";

const clients = [
  {
    title: "Oceaneering",
    src: oceaneering,
    class: "first",
  },
  {
    title: "Climatempo",
    src: climatempo,
    class: "first",
  },
  {
    title: "TenBrasil Distribuidora",
    src: tenbrasil,
    class: "first",
  },
  {
    title: "Bravo Energia",
    src: bravoenergia,
    class: "second",
  },
  {
    title: "Hospital Icara",
    src: hospitalicarai,
    class: "second",
  },
  {
    title: "Colgio Santa Mnica",
    src: santamonica,
    class: "second",
  },
  {
    title: "BID Energy",
    src: bidenergy,
    class: "second",
  },
];

const depoimentos = [
  {
    autor: "Aderson Rodrigues",
    empresa: "Mrito Energia",
    description:
      "Gostaria de fazer um breve comentrio sobre nossa amiga Silla Mota. Durante o tempo em que trabalhou conosco, na Mrito demonstrou uma dedicao excepcional s suas responsabilidades e uma habilidade notvel para superar desafios. Sua tica de trabalho exemplar e habilidades de colaborao foram inestimveis para nossa equipe e o sucesso de nossos projetos. Sem dvida, a Silla  uma pessoa excepcional e um ativo valioso para qualquer equipe.",
  },
  {
    autor: "Vinicius",
    empresa: "Nev Energia",
    description:
      "Gostaria de agradecer, em nome da NEV ENERGIA, pela incrvel contribuio que voc trouxe  nossa empresa. Os seus trabalhos contriburam para uma experincia verdadeiramente enriquecedora. Obrigado por compartilhar toda sua expertise do mundo da energia, pela competncia e humildade em abrir vrias oportunidades de trabalhos e parcerias. Voc fez a diferena e estamos extremamente gratos por isso!",
  },
  {
    autor: "Paulo Strini",
    empresa: "Grupo Triex",
    description:
      "A Silla tem sido muito eficiente como nossa assessora no mercado de Energia Eltrica, em nosso projeto de constituio da comercializadora de energia. Demonstra consistentemente conhecimento tcnico e competncias para nos orientar, sempre de maneira assertiva. Admiro sua habilidade de resolver problemas e a forma como ela lidera pessoas.  muito carismtica, reconhecida no mercado, e tem um jeito especial de identificar e juntar competncia nas pessoas, em busca de um objetivo.",
  },
  {
    autor: "lvaro Augusto",
    empresa: "Enecel Energia",
    description:
      "Silla  realmente uma pessoa notvel. Sua perspiccia e talento tm sido de grande ajuda para ns no mercado de energia. Estamos muito gratos por sua contribuio e inspirao contnuas.",
  },
];

export const Clients: FC = () => {
  return (
    <Section
      id="clientes"
      title="clientes"
      subtitle={
        <>
          veja quem <span className={styles.highlight}>confia</span> no nosso
          trabalho
        </>
      }
    >
      <Box className={styles.clientsLogos}>
        <Box className={styles.clientsRow}>
          {clients
            .filter((c) => c.class === "first")
            .map((s) => (
              <img className={styles[s.class]} src={s.src} alt={s.title} />
            ))}
        </Box>
      </Box>

      <Box className={styles.quotes}>
        <Carousel
          value={depoimentos}
          numVisible={3}
          numScroll={1}
          showIndicators={false}
          responsiveOptions={[
            {
              breakpoint: "700px",
              numVisible: 1,
              numScroll: 1,
            },
            {
              breakpoint: "1200px",
              numVisible: 2,
              numScroll: 1,
            },
          ]}
          itemTemplate={(d) => (
            <Box className={styles.quoteBox}>
              <Box
                className={styles.quote}
                sx={{ ".MuiTypography-root": { lineHeight: 0.9 } }}
              >
                <Typography
                  sx={{ fontSize: "5rem", fontWeight: 700, userSelect: "none" }}
                >
                  
                </Typography>
                <Box className={styles.texto}>{d.description}</Box>
              </Box>
              <Box className={styles.autor}> {d.autor}</Box>
              <Box className={styles.empresa}>{d.empresa}</Box>
            </Box>
          )}
        />
      </Box>
    </Section>
  );
};
